define('my-app/router', ['exports', 'ember', 'my-app/config/environment'], function (exports, Ember, config) {

	'use strict';

	var Router = Ember['default'].Router.extend({
		location: config['default'].locationType
	});

	exports['default'] = Router.map(function () {
		this.route('service');
		this.route('music');
		this.route('experience');
		this.route('faq');
		this.route('shopping');
		this.route('about');
		this.resource('posts');
	});

});